.Modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99999999;
}

.Modal .ModalWindow {
    margin: 30px auto;
    padding: 10px;
    width: 100%;
    max-width: 600px;
    max-height: 100%;
    background-color: white;
    border-radius: 10px;
    overflow: hidden;
}

.Modal .ModalBody {
    padding: 20px;
}

.Modal .ModalBody .ModalTitle {
    width: 100%;
    font-size: 25px;
}

.Modal .ModalBody .ModalContent {
    width: 100%;
    max-height: 300px;
    overflow-y: auto;
    font-size: 17px;
}

.Modal .ModalButtonArea {
    display: flex;
    padding: 0px 10px 10px 10px;
    justify-content: space-evenly;
}

.Modal .ModalButton {
    color: white;
    margin: 10px;
    width: 100%;
}

.ModalOkButton {
	background-color: #9147ff;
}

.ModalOkButton:hover {
	background-color: #8635ff;
}

.ModalOkButton:active {
	background-color: #771dff;
}

.ModalCancelButton {
    background-color: #808080;
}

.ModalCancelButton:hover {
    background-color: #ff7f35;
}

.ModalCancelButton:active {
    background-color: #ff640a;
}