.DonatePage {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}

.DonatePage .downloadInProgress {
    margin: 50px 0;
}

.DonatePage .downloadInProgress .Label {
    width: 100%;
    font-size: 20pt;
    text-align: center;
}

.DonatePage .helpButton {
    position: fixed;
    bottom: 30px;
    right: 30px;
    margin: 30px auto;
    padding: 15px 35px;
    color: black;
    background-color: #ff6720;
    text-decoration: none;
}

.DonatePage .helpButton:hover {
    background-color: #ffb120;
}

.DonatePage .donateInfo {
    margin: 20px;
    font-size: 13pt;
}

.DonatePage .ButtonArea {
    display: block;
}

.DonatePage .ButtonArea .LinkButton {
    margin: 10px;
    padding: 25px;
    width: auto;
    font-size: 20pt;
}

.DonatePage .ButtonArea .LinkButton:hover {
    background-color: #ffb120;
}

.DonatePage .thankyou {
    margin: 50px auto;
    font-size: 13pt;
    color: gray;
}

.githubButton {
    margin: auto;
    padding: 15px 35px;
    width: 100%;
    font-size: 30px;
    color: white;
    background: linear-gradient(to top right, #1a1a1a 49.5%, #414141 50.5%);
}

.githubButton:hover {
    opacity: 90%;
}

.githubButton:active {
    opacity: 80%;
}